import { getCurrentInstance } from "vue";
import { ref } from 'vue';
import { Toast } from 'vant';
export default {
  setup() {
    let {
      proxy
    } = getCurrentInstance();
    const txt = '普通配送';
    const money = 0;
    const arr = proxy.$store.state.order[proxy.$store.state.order.length - 1];
    const num = arr.num * arr.money;
    const sum = 0;
    const actions = [{
      name: '顺丰快递',
      callback: function () {
        proxy.txt = '顺丰快递', proxy.money = 30, proxy.sum = proxy.num + proxy.money;
      }
    }, {
      name: '韵达快递',
      callback: function () {
        proxy.txt = '韵达快递', proxy.money = 20, proxy.sum = proxy.num + proxy.money;
      }
    }, {
      name: '中通快递',
      callback: function () {
        proxy.txt = '中通快递', proxy.money = 10, proxy.sum = proxy.num + proxy.money;
      }
    }];
    const show = ref(false);

    const fn = () => {
      proxy.$router.back();
    };

    const address = () => {
      proxy.$router.push({
        path: "/addresslist"
      });
    };

    const onSubmit = () => Toast('点击按钮');

    return {
      fn,
      arr,
      txt,
      num,
      sum,
      show,
      money,
      actions,
      address,
      onSubmit
    };
  }

};